  font-weight: 400;
  font-style: normal;
  font-display: swap; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  padding: 0;
  margin: 0;
  min-width: 320px;
  font-family: "PT Mono", "Courier New", monospace;
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #666666; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

.page-wrapper {
  width: 100vw;
  max-width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center; }

.page-wrapper__page-footer {
  margin-top: auto; }

.page-header {
  width: 100%;
  min-height: 878px;
  background-image: url("../img/noise-desktop@1x.png");
  background-repeat: no-repeat;
  background-color: #1b2d37;
  background-size: cover;
  background-position-x: -18px; }

.page-header__content-container {
  width: 100%;
  max-width: 1472px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto; }

.page-header__main-nav {
  width: 100%;
  min-height: 118px;
  padding-left: 10px;
  padding-right: 52px;
  margin-bottom: 142px; }

.page-header__promo {
  width: 100%;
  padding-left: 6px;
  margin: 0 auto; }

@media (max-width: 1023px) {
  .page-header {
    min-height: 692.41px;
    background-image: url("../img/noise-tablet@1x.png"); } }

@media (max-width: 749px) {
