    min-height: 661.47px;
    background-image: url("../img/noise-mobile@1x.png"); } }

.logo {
  padding: 0;
  margin: 0;
  width: 202px;
  min-height: 52px;
  display: flex;
  align-items: center;
  transition: opacity 0.2s ease 0s; }

.logo[href] {
  cursor: pointer; }
  .logo[href]:hover, .logo[href]:focus {
    outline: none; }
  .logo[href]:hover svg,
  .logo[href]:focus svg {
    fill: #ffffff; }
  .logo[href]:hover span,
  .logo[href]:focus span {
    color: #ffffff; }
  .logo[href]:active {
    opacity: 0.3; }

.logo svg {
  width: 52px;
  height: 52px;
  margin-right: 16px;
  fill: #00b2ff;
  transition: fill 0.2s ease 0s; }

.logo__text {
  padding: 0;
  margin: 0;
  width: 134px;
  min-height: 52px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 11px; }

.logo b {
  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  display: block;
  width: 100%;
  min-width: 0;
  word-wrap: break-word;
  margin-bottom: 1px; }

.logo span {
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  color: #788891;
  font-family: "Roboto", "Arial", sans-serif;
  display: block;
  width: 100%;
  min-width: 0;
  word-wrap: break-word;
  transition: color 0.2s ease 0s; }

.logo--footer[href]:hover svg,
.logo--footer[href]:focus svg {
  fill: #203c42; }

.logo--footer[href]:hover span,
.logo--footer[href]:focus span {
  color: #203c42; }

.logo--footer b {
  color: #203c42; }

.user-nav {
  padding: 0;
  margin: 0;
  list-style: none;
  min-height: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }

.user-nav__item {
  flex-shrink: 0; }
  .user-nav__item--email svg {
    width: 18.62px;
    height: 13.11px;
    margin-right: 20px; }
  .user-nav__item--tel svg {
    width: 18.91px;
    height: 18.96px;
