.main-nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap; }

.main-nav__logo {
  flex-shrink: 0;
  margin-right: 8.2%; }

.main-nav__toggle {
  padding: 0;
  margin: 0;
  display: block;
  width: 37px;
  height: 13px;
  position: relative;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: opacity 0.2s ease 0s;
  margin-right: auto; }
  .main-nav__toggle::before, .main-nav__toggle::after {
    display: block;
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #ffffff;
    left: 0;
    right: 0;
    transition: background 0.2s ease 0s; }
  .main-nav__toggle::before {
    top: 0; }
  .main-nav__toggle::after {
    bottom: 0; }
  .main-nav__toggle:hover, .main-nav__toggle:focus {
    outline: none; }
  .main-nav__toggle:active {
    opacity: 0.3; }
  .main-nav__toggle:hover::before, .main-nav__toggle:focus::before, .main-nav__toggle:hover::after, .main-nav__toggle:focus::after {
    background-color: #788891; }

.main-nav__user-nav {
